<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 定义卡片标签 -->
    <el-card class="box-card">
      <!-- 2.1 定义一行元素 -->
      <el-row :gutter="20">
        <el-col :span="9">
          <!-- 2.1定义用户的输入框 -->
          <el-input placeholder="请输入内容" v-model="queryInfo.query" class="input-with-select">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>

        <el-col :span="4">
          <!-- 2.2 定义新增用户按钮-->
          <el-button type="primary" @click="centerDialogVisible = true,emptyForm(),addData()">添加用户</el-button>
        </el-col>
      </el-row>
      <!-- 参数	说明	  类型	    可选值	  默认值 -->
      <!-- visible 	是否显示 Dialog， 支持 .sync 修饰符  	boolean	—	false -->
      <!--center居中对齐  -->
      <el-dialog :visible.sync="centerDialogVisible" width="30%" center :title="titleMap[dialogTitle]" >
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleFormRef" label-width="100px"
          class="demo-ruleForm">
          <el-form-item label="用户名" prop="username">
            <el-input type="text" v-model="ruleForm.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="电话" prop="phone">
            <el-input type="text" v-model="ruleForm.phone" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input type="text" v-model="ruleForm.email" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="submit()">确 定</el-button>
        </span>
      </el-dialog>


      <el-table :data="userList" style="width: 100%" stripe border>
        <el-table-column prop="username" label="用户名"></el-table-column>
        <el-table-column prop="phone" label="电话"></el-table-column>
        <el-table-column prop="email" label="邮箱"></el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <!-- scope封装的对象 获取行级元素 row属性实行 -->
            <el-switch v-model="scope.row.status" active-color="#13ce66" inactive-color="#ff4949"
              @change="updateStatus(scope.row)">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">

              <el-button type="primary" icon="el-icon-edit" size="small" @click="updateData(scope.row)"></el-button>
              <el-button type="danger" icon="el-icon-delete" size="small" @click="deleteUserById(scope.row)"></el-button>

          </template>
        </el-table-column>
      </el-table>

      <!-- 3.定义分页功能
                                  1.@size-change  当页面的条数变化时触发
                                  2.@current-change  当页数改变时触发
                                  3.current-page  显示当前的页数 ??
                                  4.:page-sizes   显示页数的数组
                                  5.page-size     初始条件下每页的条数
                                  6.layout        显示分页的样式种类 全部显示
                                  7.total         数据的总数  ???
                                -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum" :page-sizes="[1, 2, 3,4]" :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>

    </el-card>
  </div>
</template>

<script>
  export default {
    data() {
      // rules	表单验证规则
      var validateUsername = async (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入用户名'));
        } else {
          const {
            data: result
          } = await this.$http.get('/user/legal?username=' + value)
          console.log(result)
          if (result.data) {
            callback(new Error('用户名已经存在,请更换用户名'))
          }
          callback()
        }
      };

      var validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      var validatePhone = (rule, value, callback) => {

        let regtel = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/
        if (!regtel.test(value)) {
          callback(new Error('电话号不合法!'));
        }
        callback();
      };
      var validateEmail = (rule, value, callback) => {
        let regtel = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
        if (!regtel.test(value)) {
          callback(new Error('邮箱不合法!'));
        }
        callback();
      }

      return {
        titleMap: {
          addData: "添加数据",
          updateData: "修改数据"
        },
        dialogTitle: "",
        queryInfo: {
          query: '',
          pageNum: 1,
          pageSize: 1,
          total: 0
        },
        userList: [],
        total: 0,
        centerDialogVisible: false,


        ruleForm: {
          username: '',
          password: '',
          checkPass: '',
          phone: '',
          email: '',
        },
         rules: {
                   username: [
                              { required: true, message: '请输入用户名', trigger: 'blur' },
                              { min: 3, max: 30, message: '长度在 3 到 30 个字符', trigger: 'blur' },
                                { validator: validateUsername, trigger: 'blur' }
                            ],
                   password: [
                              { required: true, message: '请输入密码', trigger: 'blur' },
                              { min: 3, max: 30, message: '长度在 3 到 30 个字符', trigger: 'blur' },
                              { validator: validatePassword, trigger: 'blur' }
                            ],
                   checkPass: [
                              { required: true, message: '请输入密码', trigger: 'blur' },
                              { min: 3, max: 30, message: '长度在 3 到 30 个字符', trigger: 'blur' },
                              { validator: validatePass2, trigger: 'blur' }
                            ],
                   phone: [
                              { required: true, message: '请输入电话', trigger: 'blur' },
                              { min: 11, max: 11, message: '长度必须11个字符', trigger: 'blur' },
                              { validator: validatePhone, trigger: 'blur' }
                            ],
                   email: [
                              { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                              { min: 3, max: 30, message: '长度在 3 到 30 个字符', trigger: 'blur' },
                              { validator: validateEmail, trigger: 'blur' }
                            ],
                }
              }
            },
    methods: {
      addData() {
        this.form = {};
        this.centerDialogVisible = true;
        this.dialogTitle = "addData";
      },
      //修改的方法
      updateData(row) {
        this.centerDialogVisible = true;
        this.dialogTitle = "updateData";
        this.ruleForm = row;
      },
      submit() {

        if (this.dialogTitle == "addData") {
          console.log(this.form);
          this.submitForm(this.ruleForm);


        } else {
          console.log(this.form);
           this.updateUser(this.ruleForm);
        }
        },
        async updateUser(ruleForm){

          const {
            data: result
          } = await this.$http.post('/user/updateUser', ruleForm)
          if (result.status !== 200) this.$message.error("修改用户失败,请重新尝试")
          this.getUserList()
          this.$message.success("修改操作成功!")
          centerDialogVisible = false
        },


        emptyForm() {
            this.ruleForm.username = '',
              this.ruleForm.password = '',
              this.ruleForm.checkPass = '',
              this.ruleForm.phone = '',
              this.ruleForm.email = ''
          },


           submitForm(ruleForm) {
              this.$refs.ruleFormRef.validate(async valid =>{
                console.log("this:"+this)
                if(!valid) return this.$message.error("请填写全部数据后再提交")
                const {
                  data: result
                } = await this.$http.post('/user/saveUser', this.ruleForm)
                if (result.status !== 200) this.$message.error("添加用户失败,请重新尝试")
                this.getUserList()
                this.$message.success("添加操作成功!")
                this.centerDialogVisible = false
              })


            },


            //模糊查询
            async getUserList() {
                const {
                  data: result
                } = await this.$http.get('/user/list', {
                  params: this.queryInfo
                })
                if (result.status !== 200) return this.$message.error("获取用户列表失败")
                this.total = result.data.total
                this.userList = result.data.rows
              },
              //当更改每页显示的数据数时 会重新调用模糊查询将数据更改
              handleSizeChange(pageSize) {
                //查询的条件需要变化
                this.queryInfo.pageSize = pageSize
                //重新查询数据
                this.getUserList()
              },
              handleCurrentChange(pageNum) {
                // 更换页面的时候 重新查询
                this.queryInfo.pageNum = pageNum
                this.getUserList()
              },
              //用户状态更改实现
              async updateStatus(user) {
                  //获取用户的Id号/状态信息 发起restFul请求.
                  //this.$http.put("/user/status/"+user.id+"/"+user.status)
                  //模板字符串写法 `` 可以编辑多行,可以直接对象取值${key}
                  //es6的高端写法
                  const {
                    data: result
                  } =
                  // 请求将id 和 当前状态传入
                  await this.$http.put(`/user/status/${user.id}/${user.status}`)
                  if (result.status !== 200) return this.$message.error("更新操作失败")
                  this.$message.success("更新操作成功!")
                  this.getUserList()
                },

                async deleteUserById(user) {
                  console.log(user)
                  const {
                    data: result
                  } = await this.$http.post('/user/deleteUserById', user)
                  if (result.status !== 200) return this.$message.error("删除操作失败")
                  this.$message.success("删除操作成功!")
                  this.getUserList()
                }

      },
      mounted() {
        this.getUserList()
      }
    }
</script>

<style lang="less" scope>
</style>
