import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login.vue'
import ElementUI from '../components/ElementUI.vue'
import Home from '../components/Home.vue'
// import User from '../components/User1.vue'
import User from '../components/User.vue'
import ItemCat from '../components/ItemCat.vue'
import itemCatParam from '../components/ItemCatParam.vue'
import item from '../components/Item.vue'
import addItem from '../components/addItem.vue'
//使用路由机制
Vue.use(VueRouter)
const routes = [{
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/elementUI',
    component: ElementUI
  },
  {
    path: '/home',
    component: Home,
    children: [{
        path: '/user',
        component: User
      },
      {
        path: '/itemCat',
        component: ItemCat
      },
      {
        path: '/itemCatParam',
        component: itemCatParam
      },
      {
        path: '/item',
        component: item
      },
      {
        path:'/item/addItem',
        component: addItem
      }
    ]
  },

]

//路由导航守卫!!!!!!!

const router = new VueRouter({
  routes
})
//  2.定义导航守卫(注意位置)
//   beforeEach: 循环遍历用户的所有的请求.(拦截)
//               在其中需要定义一个回调函数(3个参数)
//  参数介绍:  to :要访问的请求路径
//            from: 从哪个页面跳转而来
//            next: 表示请求放行
//业务需求: 1.如果用户请求/login
//         2.如果不是login则判断是否登录 token
router.beforeEach((to, from, next) => {
  if (to.path === "/login") return next()
  //获取token信息
  let token = window.sessionStorage.getItem("token")
  if (!token) return next("/login")
  next()
})

export default router
